import React, { PureComponent } from 'react';
// import { Link } from 'react-scroll';
import { push } from 'gatsby-link';
import { themeGet } from 'styled-system';
import styled from 'styled-components';

import Container from '../../components/Container';
import Box from '../../components/Box';
import Text from '../../components/Text';
import Image from '../../components/Image';
import withResponsive from '../../hoc/withResponsive';

// import BackgroundImage from '../../components/BackgroundImage';
// import ArrowDown from '../../components/ArrowDown';
import Clouds from './Clouds';
import Pigeon from './Pigeon';

import logo from './logo.svg';
import roofgarden from './roofgarden.svg';
import roofgardenmobile from './roofgardenmobile.svg';

// import relabyo from './relabyo.svg';

import SVG from '../Floors/FloorSVG';

const StyledSvg = styled(SVG)`
  .floor-btn {
    cursor: pointer;
    &:hover> {
      rect:nth-of-type(1){
        fill: ${themeGet('colors.yellow')};
      }
    }
  }
  .floor-btn-disable {
    cursor: not-allowed;
  }
  margin-top: 4em;
  @media (max-width: 415px) {
    margin-top: 17em;
  }
  @media (max-width: 376px) {
    margin-top: 14em;
  }
  @media (max-width: 321px) {
    margin-top: 10em;
  }
`;

const StyledPigeon = styled(Pigeon)`
 @media (max-width: 415px) {
    min-width: 13em;
    bottom: 32%;
  }
  @media (max-width: 376px) {
    min-width: 13em;
    bottom: 32%;
  }
  @media (max-width: 321px) {
    min-width: 10em;
    bottom: 28%;
    left: 6em;
  }
  .floor-btn {
    &:hover> {
      rect:nth-of-type(1){
        fill: ${themeGet('colors.yellow')};
      }
    }
  }
`;

const StyledBox = styled(Box)`
  @media (max-width: 321px) {
    margin-left: 1.5em;
    margin-right: 1.5em;
  }
`;

// const ArrowBox = styled(Box)`
//   margin-top: 13em;
//   @media (max-width: 415px) {
//     margin-top: 17em;
//   }
//   @media (max-width: 376px) {
//     margin-top: 14em;
//   }
//   @media (max-width: 321px) {
//     margin-top: 7.5em;
//   }
// `;

class Intro extends PureComponent {
  handleSvgLoad = () => {
    const floorBtn = this.container.querySelector('.floor-btn');
    if (floorBtn) {
      floorBtn.addEventListener('click', () => {
        push('/about');
      });
    }
  }

  handleContainerRef = (ref) => {
    this.container = ref;
  }

  render() {
    const { isMobile, ...props } = this.props;
     return (
      <Box
        position="relative"
        overflow="hidden"
        bg="blue"
        mb={['-14.5vh', null, '-12vh']}
        {...props}
        pb={['2vh', null, '1.5vh']}
        innerRef={this.handleContainerRef}
      >
        <Clouds />
        <Box is={isMobile ? undefined : Container} pt={['12vh', null, '30vh']}>
          <Box position="relative" align="center">
            <Box w="50%" maxWidth="8em" mx="auto">
              <Image src={logo} />
            </Box>
            <StyledPigeon
              position="absolute"
              w="25%"
              left={['8em', null, '2em']}
              minWidth="15em"
              bottom="40%"
              transform={['translate(-50%,-15%)', null, 'translate(-50%,-10%)']}
              onClick={() => push('/contact')}
              zIndex={8}
            />
            <Text.h6 fontWeight="200" mt={['0.5em', null, '1.5rem']} mx="2em">
              <StyledBox
                borderTop="1px solid white"
                my="4em"
                mx={['3em', '10em', '8em', '14em', '15em']}
                py="1em"
                borderBottom="1px solid white"
                color="white"
                f={['1em', null, '1.25em']}
              >
                透過資訊設計 建構創新的溝通模式
              </StyledBox>
            </Text.h6>
            {/* <ArrowBox pb="2em">
              <Text.h6 fontWeight="200" my="1rem">
                Take a Peek
              </Text.h6>
              <Link
                to="4"
                spy
                smooth
                duration={1000}
              >
                <ArrowDown mt="-0.5em" w="3.5em" mx="auto" />
              </Link>
            </ArrowBox> */}
            <StyledSvg
              src={isMobile ? roofgardenmobile : roofgarden}
              ratio={isMobile ? 530.45 / 751 : 531.03 / 1151.32}
              onLoad={this.handleSvgLoad}
            />
          </Box>
        </Box>
      </Box>
    )
  }
};

export default withResponsive(Intro);



