import React from 'react';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import floor from './floor-2.svg';
import SVG from '../../FloorSVG';
import floors from '../../../Elevator/floors.json';

import projects from '../../projects/2';
// import getAutoLinkFloor from '../../getAutoLinkFloor';
import getGalleryFloor from '../../getGalleryFloor';

import Container from '../../../../components/Container';


const StyledSvg = styled(SVG)`
  .floor-btn {
    cursor: pointer;
    /* &:hover {
      > path:nth-of-type(1) {
        fill: ${themeGet('colors.yellow')};
      }
    } */
  }
  .floor-item image,
  .floor-item rect,
  .floor-item text {
    cursor: pointer;
  }
  .floor-btn-disabled image,
  .floor-btn-disabled.floor-btn {
    cursor: not-allowed;
  }
  .floor-morebtn {
    cursor: pointer;
    &:hover {
      rect {
        fill: ${themeGet('colors.yellow')};
      }
      text {
        fill: black;
      }
    }
  }
`;

const AutoFloor = getGalleryFloor(floors[2].key, projects, 91);

class Floor2 extends AutoFloor {
  render() {
    return (
      <Container position="relative" zIndex={3} innerRef={this.handleContainerRef}>
        <StyledSvg
          src={floor}
          onLoad={this.handleSvgLoad}
          ratio={650.94 / 1192.5}
        />
      </Container>
    );
  }
}

export default Floor2;



