import round from 'lodash/round';

import getFloor from './getFloor';

export default (floor, projects, imageContainerWidth) => {
  const Floor = getFloor(floor);
  class GalleryFloor extends Floor {
    componentDidMount() {
      // 預先讀取全部的照片
      projects.forEach(({ src }, index) => {
        const img = new Image();
        img.onload = () => this.handleImageFetch(index)(img);
        img.src = src;
      });
    }

    getNewScale = (width, transform) => {
      const [, oldScale] = /scale\(([^)]+)/.exec(transform);
      return transform.replace(oldScale, round(imageContainerWidth / width, 2));
    }

    // 取好預設的 this.imageUris 為空陣列
    imageUris = []

    imageEles = []

    tries = 0

    // 把讀到的datauri存到 this.imageUris 裡
    handleImageFetch = (index) => (imgData) => {
      this.imageUris[index] = imgData;
      this.handleImageReady(index);
    }

    handleImageReady = (i) => {
      if (this.domLoaded) {
        const img = this.imageEles[i];
        img.setAttribute('xlink:href', this.imageUris[i].src);
        img.setAttribute('width', this.imageUris[i].width);
        img.setAttribute('height', this.imageUris[i].height);
        img.setAttribute('transform', this.getNewScale(this.imageUris[i].width, img.getAttribute('transform')));
        if (projects[i].link) {
          img.addEventListener('click', () => window.open(projects[i].link, '_blank'));
        }
      } else {
        window.setTimeout(() => this.handleImageReady(i), 100);
      }
    }

    handleSvgLoad() {
      // super，讀取 Floor 中寫好的 handleSvgLoad
      super.handleSvgLoad();
      const floorBtns = this.container.querySelectorAll('.floor-btn');
      if (floorBtns) {
        floorBtns.forEach((btn, index) => {
          btn.querySelector('text').innerHTML = projects[index].title;
          if (projects[index].link) {
            btn.addEventListener('click', () => window.open(projects[index].link, '_blank'));
          } else {
            btn.classList.add('floor-btn-disabled');
          }
        });
      }
      // 在html中，尋找所有的 .floor-item
      const floorItems = this.container.querySelectorAll('.floor-item');
      if (floorItems) {
        floorItems.forEach((item, i) => {
          this.imageEles[i] = item.querySelector('image');
          if (!projects[i].link) {
            item.classList.add('floor-btn-disabled');
          }
        });
      }
      this.domLoaded = true;
    }

    checkLoaded = () => {
      if (this.loaded) return true;
      this.loaded = this.domLoaded && this.imageUris.every(Boolean);
      return this.loaded;
    }
  }

  return GalleryFloor;
};




