import React from 'react';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import floor from './floor-b1.svg';
import SVG from '../../FloorSVG';
import floors from '../../../Elevator/floors.json';

import getSliderFloor from '../../getSliderFloor';

import Box from '../../../../components/Box';

import projects from '../../projects/b1';

const StyledSvg = styled(SVG)`
  .floor-btn {
    cursor: pointer;
    &:hover {
      path:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
      }
      polygon {
        fill: white;
        stroke: white;
      }
    }
  }
  .floor-morebtn {
    cursor: pointer;
    &:hover {
      rect:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
      }
      text {
        fill: black;
      }
    }
  }
`;

const AutoFloor = getSliderFloor(floors[4].key, projects, 242.6);

class FloorB1 extends AutoFloor {
  handleSvgLoad = () => {
    super.handleSvgLoad();
    // this.title2 = this.container.querySelector('.floor-title-2');
    this.client = this.container.querySelector('.floor-client');
    // this.group = this.container.querySelector('.floor-content');
    // this.container.querySelector('.floor-link')
    //   .addEventListener('click', () => window.open(projects[this.state.current].link, '_blank'));
  }

  tries1 = 0

  // updateSvgContent = (index) => {
  //   if (!this.checkLoaded()) {
  //     setTimeout(() => {
  //       this.tries1 += 1;
  //       this.updateSvgContent(index);
  //     }, this.tries1 * 100);
  //   } else {
  //     super.updateSvgContent(index);
  //     const { title, client } = projects[index];
  //     this.client.innerHTML = client;
  //     const [title1, title2] = title.split('|');
  //     if (title2) {
  //       this.title.innerHTML = title2;
  //       this.title2.innerHTML = title1;
  //       this.group.setAttribute('transform', 'translate(0, 30)');
  //     } else {
  //       this.title2.innerHTML = '';
  //       this.group.setAttribute('transform', '');
  //     }
  //   }
  // }

  render() {
    return (
      <Box innerRef={this.handleContainerRef} bg="lightGray">
        <StyledSvg
          src={floor}
          onLoad={this.handleSvgLoad}
          ratio={598.3 / 752}
        />
      </Box>
    );
  }
}

export default FloorB1;



