import React from 'react';
import { Link as ScrollLink } from 'react-scroll';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import Box from '../../../components/Box';
import Link from '../../../components/Link';
import Text from '../../../components/Text';
import DoubleLayerBox from '../../../components/DoubleLayerBox';
import Border from '../../../components/Border';
import FloorButton from '../FloorButton';
import floors from '../floors.json';

const StyledLink = styled(Link)`
  &:hover {
    .elevator-button {
      background: ${themeGet('colors.yellow')};
    }
  }
`;

const StyledScrollLink = styled(ScrollLink)`
  cursor: pointer;
  &:hover {
    color: ${themeGet('colors.blue')};
    .elevator-button {
      background: ${themeGet('colors.yellow')};
    }
  }
`;

export default ({ visible, onSetActiveFloor, currentFloor }) => (
  <Box
    position="fixed"
    right="2em"
    top="50vh"
    transform="translateY(-50%)"
    opacity={Number(visible)}
    pointerEvents={visible ? 'auto' : 'none'}
    zIndex={99}
    transition="opacity 500ms"
  >
    <Box
      position="absolute"
      top="0"
      left="0"
      right="0"
      bottom="0"
      bg="lightGray"
      transform="translate(-8%, 2%)"
    />
    <DoubleLayerBox position="relative" zIndex={5}>
      <Box px="1.5em" py="1em">
        <Box bg="white" p="0.5em" w="5.5em" mx="auto" mb="1em" border="1px solid" borderColor="lightBlue">
          <Border borderBottom="1px solid" borderColor="lightBlue">
            <Text.dosis align="center" f="3em" color="blue">{currentFloor}</Text.dosis>
          </Border>
        </Box>
        <Box>
          {floors.map((f) => (
            <StyledScrollLink
              key={f.floor}
              to={f.floor}
              spy
              smooth
              duration={1000}
              offset={typeof window === 'undefined' ? 0 : -window.innerHeight / 4}
              onSetActive={onSetActiveFloor}
            >
              <FloorButton label={f.floor} active={f.floor === currentFloor}>
                {f.text}
              </FloorButton>
            </StyledScrollLink>
          ))}
          {['5', 'B2'].map((key) => (
            <StyledScrollLink
              key={key}
              to={key}
              spy
              offset={typeof window === 'undefined' ? 0 : -window.innerHeight / 4}
              onSetActive={onSetActiveFloor}
            />
          ))}
          {/* <StyledLink to="/portfolio">
            <FloorButton label="All">
              所有作品
            </FloorButton>
          </StyledLink> */}
        </Box>
        {/* <Flex align="center" justify="center">
          {contacts.map((contact, index) => (
            <Box f="0.8em" w="4.5em" mt="1em" key={index}>
              <Box mx="10%">
                <LinksButton
                  to={contact.to}
                  bg="transparent"
                  hoverBg="yellow"
                  w={1}
                  px="0"
                  py="0"
                >
                  <BackgroundImage src={contact.img} ratio={13 / 17} />
                </LinksButton>
              </Box>
              <Border borderBottom="1px solid lightBlue" mx="0.25em" mt="0.5em">
                <Text>{contact.text}</Text>
              </Border>
            </Box>
          ))}
        </Flex> */}
      </Box>
    </DoubleLayerBox>
  </Box>
);



