import round from 'lodash/round';

import getFloor from './getFloor';

export default (floor, projects, imageContainerWidth) => {
  const Floor = getFloor(floor);
  const projectCount = projects.length;
  class AutoSliderFloor extends Floor {
    constructor(props) {
      super(props);
      this.updateSvgContent = this.updateSvgContent.bind(this);
    }

    componentDidMount() {
      // 預先讀取全部的照片
      projects.forEach(({ src }, index) => {
        const img = new Image();
        img.onload = () => this.handleImageFetch(index)(img);
        img.src = src;
      });
    }

    getNewScale = (width) => {
      const [, oldScale] = /scale\(([^)]+)/g.exec(this.imageTransform);
      return this.imageTransform.replace(`scale(${oldScale})`, `scale(${round(imageContainerWidth / width, 2)})`);
    }

    // 取好預設的 this.imageUris 為空陣列
    imageUris = new Array(projectCount).fill(false)

    tries = 0

    updateSvgContent(index) {
      if (!this.checkLoaded()) {
        setTimeout(() => {
          this.tries += 1;
          this.updateSvgContent(index);
        }, this.tries * 100);
      } else {
        if (this.title) {

          // 更新title的文字
          this.title.innerHTML = projects[index].title;
        }

        if (this.image) {
          // 更新image
          this.image.setAttribute('xlink:href', this.imageUris[index].src);
          this.image.setAttribute('width', this.imageUris[index].width);
          this.image.setAttribute('height', this.imageUris[index].height);
          this.image.setAttribute('transform', this.getNewScale(this.imageUris[index].width));
        }
        this.currentIndex = index;
      }
    }

    handleBtnClick = (index) => () => {
      if (!this.updateSvgContent) return;

      if (index === 0) {
        // 向後
        if (this.currentIndex === 0) {
          this.updateSvgContent(projectCount - 1);
        } else {
          this.updateSvgContent(this.currentIndex - 1);
        }
      } else {
        // 向前
        if (this.currentIndex === projectCount - 1) {
          this.updateSvgContent(0);
        } else {
          this.updateSvgContent(this.currentIndex + 1);
        }
      }
    }

    // 把讀到的datauri存到 this.imageUris 裡
    handleImageFetch = (index) => (imgData) => {
      this.imageUris[index] = imgData;
      if (index === 0) {
        this.updateSvgContent(0);
      }
    }

    handleImageOnClick = () => projects[this.currentIndex].link && window.open(projects[this.currentIndex].link, '_blank')

    handleSvgLoad() {
      // super，讀取 Floor 中寫好的 handleSvgLoad
      super.handleSvgLoad();
      const floorBtns = this.container.querySelectorAll('.floor-btn');
      if (floorBtns) {
        floorBtns.forEach((btn, index) => {
          btn.addEventListener('click', this.handleBtnClick(index));
        });
      }
      // 在html中，尋找唯一的image tag
      this.image = this.container.querySelector('image');
      if (this.image) {
        this.imageTransform = this.image.getAttribute('transform');
        this.imageOnClick = this.image.addEventListener('click', this.handleImageOnClick);
      }
      this.label = this.container.querySelector('.floor-label');
      if (this.label) {
        this.label.addEventListener('click', this.handleImageOnClick);
        this.title = this.label.querySelector('text');
      }
      this.domLoaded = true;
    }

    checkLoaded = () => {
      if (this.loaded) return true;
      this.loaded = this.domLoaded && this.imageUris.every(Boolean);
      return this.loaded;
    }
  }

  return AutoSliderFloor;
};




