import React from 'react';
import { Element } from 'react-scroll'
import set from 'lodash/set';

import Box from '../components/Box';

import DeskElevatorButton from '../containers/Elevator/ElevatorButton';
import MobileElevatorButton from '../containers/Elevator/MobileElevatorButton';
import Header from '../containers/Header';
import Intro from '../containers/Intro';
import Partners from '../containers/Partners';
import DesktopFloors from '../containers/Floors/Desktop';
import MobileFloors from '../containers/Floors/Mobile';

import withResponsive from '../hoc/withResponsive';

const indexes = ['5', '4', '3', '2', '1', 'B1', 'B2'].reduce((idx, key, index) => set(idx, key, index), {});

class Page extends React.PureComponent {
  state = {
    elevatorVisible: false,
    currentFloor: '5',
  }

  handleSetActiveFloor = (nextFloor) => {
    const { currentFloor } = this.state;
    if (nextFloor !== currentFloor) {
      const newState = { currentFloor: nextFloor };
      if (currentFloor === '5' && indexes[nextFloor] > 0) newState.elevatorVisible = true;
      if (currentFloor === 'B2' && indexes[nextFloor] < indexes.B2) newState.elevatorVisible = true;
      if (indexes[currentFloor] > 0 && nextFloor === '5') newState.elevatorVisible = false;
      if (indexes[currentFloor] < indexes.B2 && nextFloor === 'B2') newState.elevatorVisible = false;
      this.setState(newState);
    }
  }

  render() {
    const { browser } = this.props;
    const { elevatorVisible, currentFloor } = this.state;
    const isMobile = browser.lessThan.sm;
    const Floors = isMobile ? MobileFloors : DesktopFloors;
    const ElevatorButton = isMobile ? MobileElevatorButton : DeskElevatorButton;
    return (
      <Box overflow="hidden">
        <Header isHome currentFloor={currentFloor} />
        <Element name="5">
          <Intro />
        </Element>
        <Floors currentFloor={currentFloor} />
        <Element name="B2">
          <Partners />
        </Element>
        <ElevatorButton
          visible={elevatorVisible}
          currentFloor={currentFloor}
          onSetActiveFloor={this.handleSetActiveFloor}
        />
      </Box>
    );
  }
}

export default withResponsive(Page);



