import React from 'react';
import { Link as ScrollLink } from 'react-scroll';
import styled from 'styled-components';
import { themeGet } from 'styled-system';
// import get from 'lodash/get';

import Box from '../../../components/Box';
import Link from '../../../components/Link';
import Flex from '../../../components/Flex';
import theme from '../../../components/ThemeProvider/theme';
// import BackgroundImage from '../../../components/BackgroundImage';

import FloorButton from '../FloorButton';
import floors from '../floors.json';
import Links from '../../Header/Links';

const StyledLink = styled(Link)`
  width: 3.25em;
  &:hover {
    .elevator-button {
      background: ${themeGet('colors.yellow')};
    }
  }
`;

const StyledScrollLink = styled(ScrollLink)`
  cursor: pointer;
  width: 3.25em;
  &:hover {
    color: ${themeGet('colors.blue')};
    .elevator-button {
      background: ${themeGet('colors.yellow')};
    }
  }
`;

export default ({ visible, onSetActiveFloor, currentFloor }) => (
  <Box
    position="fixed"
    top="0"
    left="0"
    right="0"
    opacity={Number(visible)}
    pointerEvents={visible ? 'auto' : 'none'}
    zIndex={99}
    transition="opacity 500ms"
    bg="#f7f7f7"
    height={theme.headerHeight}
  >
    <Flex height="100%" justify="center" align="center">
      {floors.map((f) => (
        <StyledScrollLink
          key={f.floor}
          to={f.floor}
          spy
          smooth
          duration={1000}
          offset={typeof window === 'undefined' ? 0 : -window.innerHeight / 4}
          onSetActive={onSetActiveFloor}
        >
          <FloorButton mx="0.25rem" label={f.floor} active={f.floor === currentFloor} />
        </StyledScrollLink>
      ))}
      <Links isHome />
    </Flex>
    {['5', 'B2'].map((key) => (
      <StyledScrollLink
        key={key}
        to={key}
        spy
        offset={typeof window === 'undefined' ? 0 : -window.innerHeight / 4}
        onSetActive={onSetActiveFloor}
      />
    ))}
  </Box>
);



