import React from 'react';
import Box from '../../components/Box';
import Cloud from '../../components/Cloud';

const locations = [
  {
    w: ['30%', null, '15%'],
    top: ['48%', null, '10%'],
    right: ['-15%', null, '10%'],
  },
  {
    w: ['30%', null, '25%'],
    top: ['15%', null, '2%'],
    left: ['-12%', null, '5%'],
  },
  {
    w: [0, null, '13%'],
    top: '45%',
    left: '-5%',
  },
  {
    w: [0, null, '15%'],
    top: '47%',
    right: '-9%',
  },
];

export default (props) => (
  <Box {...props}>
    {locations.map((location, index) => (
      <Cloud
        key={index}
        position="absolute"
        top={location.top}
        right={location.right}
        left={location.left}
        w={location.w}
      />
    ))}
  </Box>
);



