import React from 'react';
import styled from 'styled-components';
import { themeGet } from 'styled-system';

import getSliderFloor from '../../getSliderFloor';
import floor from './floor-3.svg';
import SVG from '../../FloorSVG';

import projects from '../../projects/3';
import floors from '../../../Elevator/floors.json';

import Container from '../../../../components/Container';


const StyledSvg = styled(SVG)`
  .floor-btn {
    cursor: pointer;
    &:hover {
      rect:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
      }
      polygon {
        fill: white;
        stroke: white;
      }
    }
  }
  image,
  .floor-label {
    cursor: pointer;
  }
  .floor-morebtn {
    cursor: pointer;
    &:hover {
      rect:nth-of-type(2) {
        fill: ${themeGet('colors.yellow')};
      }
      text {
        fill: black;
      }
    }
  }
`;

const SliderFloor = getSliderFloor(floors[1].key, projects, 576.32);

class Floor3 extends SliderFloor {
  render() {
    return (
      <Container innerRef={this.handleContainerRef} position="relative">
        <StyledSvg
          src={floor}
          ratio={665.52 / 1192.5}
          onLoad={this.handleSvgLoad}
        />
        {/* <Box position="absolute" top="0" right="9.5%" left="9.5%">
          <Animatable animate src={bear} ratio={555.23 / (4245.12 / 5)} />
        </Box> */}
      </Container>
    );
  }
}

export default Floor3;



